.container{
    text-align: center;
    overflow-x: hidden;
}

.title{
    margin-top: 20px;
    color: white;
    font-size: 4vw;
    display: flex;
    justify-content: center;
    align-items: center;
 
   
}

.title span{
    color:#26ffcb ;
    padding-left: 15px;
}

.btn {
  --color1: #1f3540;
  --color2: #26ffcd;
  perspective: 1000px;
  padding: 1em 1em;
  background: linear-gradient(var(--color1), var(--color2));
  border: none;
  outline: none;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 4px;
  color: #fff;
  text-shadow: 0 10px 10px #000;
  cursor: pointer;
  transform: rotateX(70deg) rotateZ(30deg);
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.btn::before {
  content: "";
  width: 100%;
  height: 15px;
  background-color: var(--color2);
  position: absolute;
  bottom: 0;
  right: 0;
  transform: rotateX(90deg);
  transform-origin: bottom;
}

.btn::after {
  content: "";
  width: 15px;
  height: 100%;
  background-color: var(--color1);
  position: absolute;
  top: 0;
  right: 0;
  transform: rotateY(-90deg);
  transform-origin: right;
}

.btn:hover {
  transform: rotateX(30deg) rotateZ(0);
}

.boxes{
    display: flex;
    height: 100px;
    width: 100px;
    background:#1f3540 ;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
}

.board{
    height: 350px;
    width: 500px;
    display: flex;
    margin: auto;
    justify-content: center;
}

.boxes img {
    margin: 30px;
}
.title img{
    padding: 20px 20px;
    height: 50px;
}